<template>
  <v-app id="app">
    <router-view/>
  </v-app>
</template>

<script>
export default {
  name: 'App',
    created() {
        console.log('启动:')

        const needAuth = this.$store.getters.checkToken()
        if (needAuth) {
          this.conn.token().then(res => {
              console.log(res)
              this.$store.commit('changeAuth', res.jwt, res.user)
          })
        }

    },
    mounted() {
        console.log('完毕 》》》》》')
    }
}
</script>

<style lang="sass">
@import "@/common/sass/base.sass"
</style>



