
import conf from '../../conf'
import service from '../network/service'

const url = conf.baseURL + '/auth/local'

export default {
  async token(username = 'vcrting@163.com', password = '1234567') {
    const condition = {
      identifier: username,
      password: password,
    }
    
    return await service.post(url, condition)
  }
}


