
const Bonus = () => import('@/pages/Bonus/Bonus')
const ActiveHouseBonus = () => import('@/pages/Bonus/active_house/ActiveHouseBonus')
const RecommendBonus = () => import('@/pages/Bonus/recommend/RecommendBonus')

export default {
    
    path: '/home/bonus',
    component: Bonus,
    
    redirect:'/home/bonus/active_house',
    children: [
        {
            path: '/home/bonus/active_house',
            component: ActiveHouseBonus,
        },
        {
            path: '/home/bonus/recommend',
            component: RecommendBonus,
        }
    ]
}


