
import bonusRouter from './children/bonus'
// import authRouter from './children/auth'

const Home = () => import('@/pages/Home/Home')

const AllRoutes = [
    {
      path: '/home',
      component: Home,
      redirect: '/home/bonus',
      children: [
        bonusRouter
      ]
    },
    // ...authRouter,
    {
      path: '',
      redirect: '/home',
      meta: {
        title: '首页'
      }
    }
]


export default AllRoutes


